html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

:root {
  --font-montserrat: "Montserrat", serif;
  --montserrat-500: 500;
  --montserrat-700: 700;
  --font-fraunces: "Fraunces", serif;
  --fraunces-700: 700;
}

.container {
  position: relative;
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: hsl(30, 38%, 92%);
}
@media (min-width: 71.875em) {
  .container {
    max-width: 100vw;
  }
}

.product-card {
  background-color: hsl(0, 0%, 100%);
}

/* Prevent font size inflation */
html {
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
}

/* Remove default margin in favour of better control in authored CSS */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin-block-end: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  line-height: 1.5;
}

/* Set shorter line heights on headings and interactive elements */
h1,
h2,
h3,
h4,
button,
input,
label {
  line-height: 1.1;
}

/* Balance text wrapping on headings */
h1,
h2,
h3,
h4 {
  text-wrap: balance;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
  color: currentColor;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
}

/* Make sure textareas without a rows attribute are not tiny */
textarea:not([rows]) {
  min-height: 10em;
}

/* Anything that has been anchored to should have extra scroll margin */
:target {
  scroll-margin-block: 5ex;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.product-card {
  max-width: 31.25rem;
  border-radius: 0.625rem;
  overflow: hidden;
}
@media (min-width: 40em) {
  .product-card {
    display: flex;
    max-width: 37.5rem;
    max-height: 31.25rem;
  }
}
@media (min-width: 71.875em) {
  .product-card {
    display: flex;
    max-width: 43.75rem;
    min-height: 31.25rem;
  }
}
@media (min-width: 71.875em) {
  .product-card__image {
    background-image: url("/images/image-product-desktop.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    flex: 1;
  }
  .product-card__image img {
    display: none;
    flex: 1;
  }
}
@media (min-width: 40em) {
  .product-card__image {
    background-image: url("/images/image-product-desktop.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    flex: 1;
  }
  .product-card__image img {
    display: none;
    flex: 1;
  }
}
.product-card__tag {
  font-family: var(--font-montserrat);
  font-weight: var(--montserrat-500);
  text-transform: uppercase;
  font-size: 0.75rem;
  font-size: clamp(0.75rem, 0.625rem + 0.625vw, 0.875rem);
  letter-spacing: 0.3125rem;
  color: hsl(228, 12%, 48%);
}
.product-card__title {
  font-family: var(--font-fraunces);
  font-weight: var(--fraunces-700);
  font-size: 1.125rem;
  font-size: clamp(1.125rem, 0.5rem + 3.125vw, 1.75rem);
  color: hsl(212, 21%, 14%);
}
.product-card__description {
  font-family: var(--font-montserrat);
  font-weight: var(--montserrat-500);
  font-size: 0.75rem;
  font-size: clamp(0.75rem, 0.5rem + 1.25vw, 1rem);
  color: hsl(228, 12%, 48%);
}
.product-card__discounted {
  font-family: var(--font-fraunces);
  font-weight: var(--fraunces-700);
  font-size: 1rem;
  font-size: clamp(1rem, 0.25rem + 3.75vw, 1.75rem);
  color: hsl(158, 36%, 37%);
}
.product-card__original {
  font-family: var(--font-montserrat);
  font-weight: var(--montserrat-500);
  font-size: 0.75rem;
  font-size: clamp(0.75rem, 0.625rem + 0.625vw, 0.875rem);
  color: hsl(228, 12%, 48%);
}
.product-card__button {
  font-family: var(--font-montserrat);
  font-weight: var(--montserrat-700);
  font-size: 1rem;
  color: hsl(0, 0%, 100%);
  background-color: hsl(158, 36%, 37%);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  border-radius: 0.625rem;
  border: none;
}
.product-card__button:hover {
  background-color: hsl(158, 42%, 18%);
  cursor: pointer;
}
.product-card__button-icon {
  color: hsl(0, 0%, 100%);
}
.product-card__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2.5rem;
  padding: 1.5625rem;
  flex: 1;
}
@media (min-width: 71.875em) {
  .product-card__content {
    justify-content: flex-start;
  }
}
.product-card__price {
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}
@media (min-width: 71.875em) {
  .product-card__price {
    gap: 2rem;
  }
}